$dark-background-color: #1c1f2b !default;
$dark-background-alt-color: rgb(45, 49, 67) !default;
$dark-text-color: rgb(216, 215, 215) !default;
$heading-lapel-color: #3d4256;

[data-theme="dark"] {
  background-color: $dark-background-color;

  :root {
    color-scheme: dark;
  }
  .page-content {
    color: $dark-text-color;

    // h2,
    // h3,
    // h4,
    // h5,
    // h6 {
    // 	background-color: $dark-background-color;
    // }

    // section {
    // 	background-color: $dark-background-color;
    // }
  }

  @media only screen and (min-width: 800px) {
    h1,
    h2,
    h3,
    h4,
    blockquote {
      &:before {
        color: $heading-lapel-color;
      }
    }
  }
  .post-meta {
    border-left-color: gray;
  }

  main a {
    &:visited {
      color: #3d88c6;
    }
  }

  .offcanvas-body,
  #socialList,
  main,
  header,
  footer {
    a {
      // color: rgb(140, 178, 228);
      color: #61afef;

      &:hover {
        color: #74a6cf;
      }
    }
  }

  main a.category-link {
    color: gray;
    &:hover {
      // color: #61afef;
      color: white;
      border-color: white;
    }
  }

  // h1,
  // h2,
  // h3,
  // h4,
  // h5,
  // h6 {
  // 	color: white;
  // }

  // .site-title,
