  // 	color: white;
  // }

  // *:not(pre) > code {
  // 	color: $dark-text-color;
  // 	background-color: #454545;
  // }

  table {
    color: #9d9d9d;

    th {
      background-color: #050505;
    }

    tr:nth-child(even) {
      background-color: #080808;
    }
  }

  blockquote {
    color: $dark-text-color;
    border: 1px solid #3d4047;
    border-left: 8px solid #3d4047;
  }

  strong {
    color: white;
  }

  .site-header,
  .site-footer {
    background-color: $dark-background-color;

    .footer-heading a {
      color: white;
    }

    .site-brand {
      .site-brand-inner {
        &,
        &:visited {
          color: #f8f8f8;
        }
      }
    }

    @media only screen and (max-width: 600px) {
      .site-nav {
        background-color: $dark-background-alt-color;
      }
    }

    .site-nav .page-link {
      color: #f8f8f8;
    }

    .menu-icon {
      // background-color: $dark-background-color;
      opacity: 0px;
    }

    // @include media-query($on-laptop) {
    // 	.menu-icon > svg {
    // 		fill: rgba($dark-text-color, 80%);
    // 	}
    // }
  }

  .post-nav-pn a:hover {
    transition: #367bdb 500ms cubic-bezier(0.08, 0.52, 0.52, 1);
    border-color: #367bdb;
    text-decoration: none;
  }

  // .pagination {
  // 	.post-link {
  // 		color: $dark-text-color;
  // 	}

  // 	.post-title {
  // 		a:visited:after {
  // 			background-color: $dark-background-color;
  // 		}

  // 		a:after {
  // 			color: $dark-background-color;
  // 		}
  // 	}

  // 	.post-list {
  // 		& > li:not(:last-child) {
  // 			border-bottom: 1px solid #545454;
  // 		}
  // 	}

  // 	.post-tags .post-tag:hover {
  // 		color: #d7d7d7;
  // 	}
  // }

  // .page-segments {
  // 	li {
  // 		a {
  // 			color: #ddd;
  // 		}

  // 		a:visited:after {
  // 			background-color: $dark-background-color;
  // 		}

  // 		a:after {
  // 			color: $dark-background-color;
  // 		}
  // 	}
  // }

  .post .post-content {
    img:not([raw]) {
      background-color: #ffffff33;
    }
  }

  .post-related {
    & > *:first-child {
      color: #d7d7d7;
    }

    a:visited:after {
      background-color: $dark-background-color;
    }

    a:after {
      color: $dark-background-color;
    }

    a:hover {
      color: #aaa;
    }
  }

  logo {
    filter: invert(100%);
  }

  pre {
    background-color: #282c3e;

    code {
      color: white !important;
    }
  }

  .html {
    filter: invert(85%);
  }

  .text-muted {
    color: gray !important;
  }

  .navbar-toggler {
    border-color: gray;
    background-color: $dark-background-alt-color;
    .navbar-toggler-icon {
      filter: invert(85%);
    }
  }

  #offcanvasNavbar {
    background-color: $dark-background-alt-color;
    .btn-close {
      filter: invert(85%);
    }
    #darkSwitch {
      background-color: #3e90d4;
    }

    @media only screen and (max-width: 992px) {
      #nav-links {
        background-color: $dark-background-alt-color;
        border: #777777 solid 1px;
        border-radius: 0.5em;
      }
    }
    .offcanvas-title,
    .toc-title {
      color: white;
    }
  }

  .page-link {
    color: white;
    &:hover {
      color: white;
    }
  }

  hr {
    border-color: white;
  }

  #offcanvasToggleButton {
    background-color: $dark-background-color;
    border-color: gray;
    svg {
      fill: #aaaaaa;
    }
  }

  @mixin dark-scroll {
    /* Track */
    &::-webkit-scrollbar-track {
      background: #2a2c36;
      border-left: none;
    }
    /* Handle */
    &::-webkit-scrollbar-thumb {
      background: #7a7c85;
      border-radius: 6em;
    }
    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
      background: #848484;
    }
  }

  .offcanvas-body {
    @include dark-scroll;
  }
  @include dark-scroll;
}

