$background-color: white !default;
$heading-lapel-color: #d7d7d7 !default;
$light-text: rgba(227, 227, 227, 0.5) !default;
$active-color: #7aa0d1;
$lg-breakpoint: 992px; /* lg breakpoint */
$xl-breakpoint: 1200px; /* xl breakpoint */

//default
:root {
  --theme-color: rgb(27, 114, 97) 0;
  /* --theme-color: #db4b4b; */
}

[data-theme="dark"] {
  --theme-color: #4cd899;
}

// * General -------------------------------------------------------------------

blockquote {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  padding: 20px 20px;
  font-size: 140%;
  // background-color: #f7f7f7;
  border: 1px solid #e8e8e8;
  border-left: 8px solid #e8e8e8;
  font-style: italic;
  p {
    font-weight: 300;
  }
}

blockquote li {
  margin: 15px 15px;
}

main hr {
  margin-top: 45px;

  margin-bottom: 45px;
}

html {
  scroll-behavior: smooth;
}

.page-content {
  line-height: 1.7;
  // padding-top: 40px;

  .page-title,
  .post-title {
    font-size: 38px;
    line-height: 1.5;
    margin-top: 0.3em;
    // margin-bottom: 0.85em;
    margin-bottom: 0.5em;
  }
  // .post-title {
  //   margin-bottom: 0;
  // }
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-top: 1.275em;
    margin-bottom: 0.85em;
    font-weight: 450;
  }

  h1,
  h2 {
    font-weight: 600;
  }

  h4 {
    font-size: 1.5em;
  }

  dl,
  ol,
  .post-content > p,
  table,
  blockquote {
    // margin-top: 0;
    margin-bottom: 1.5em;
  }

  .post-content > p {
    text-align: justify;
  }

  pre + * {
    // Any page-content element preceding a pre
    margin-top: 1.5em;
  }

  pre {
    margin: 0px !important;
  }
}

input {
  cursor: pointer;
}

@media only screen and (min-width: $lg-breakpoint) {
  h1:before {
    content: "H1";
    color: $heading-lapel-color;
    font-size: 0.8em;
    font-weight: initial;
    margin-left: -52px;
    margin-right: 15px;
  }
  h2:before {
    content: "H2";
    color: $heading-lapel-color;
    font-size: 0.8em;
    font-weight: initial;
    margin-left: -54px;
    margin-right: 20px;
  }
  h3:before {
    content: "H3";
    color: $heading-lapel-color;
    font-size: 0.8em;
    font-weight: initial;
    margin-left: -51px;
    margin-right: 23px;
  }
  h4:before {
    content: "H4";
    color: $heading-lapel-color;
    font-size: 0.8em;
    font-weight: initial;
    margin-left: -40px;
    margin-right: 20px;
  }
  blockquote:before {
    content: "❝";
    position: absolute;
    color: $heading-lapel-color;
    font-size: 2em;
    line-height: 1em;
    margin-left: -1.9em;
    margin-top: -0em;
  }
}

.site-footer .site-nav .trigger {
  height: 40px;
}

.post-meta {
  color: gray;
  //   border-left: 2px solid lightgray;
  //   padding: 0.4em 0px 0.4em 1.5em;
  & > div {
    display: inline-block;
    margin-right: 1em;
    @media only screen and (min-width: $lg-breakpoint) {
      margin-right: 2em;
    }
  }
}

#offcanvasNavbar .post-meta {
  text-align: left;
  border-left: 2px solid lightgray;
  color: gray;
  padding: 5px 0px 5px 1em;
  // padding-left: 18px;
  // margin-top: 20px;
}

.post-extlink {
  display: block;
  margin: 60px auto;
  padding: 20px;
  text-align: center;
  border: 2px solid gray;
  border-radius: 10px;
  font-size: large;
  &:hover {
    text-decoration: none;
    border-color: $active-color;
  }
}

blockquote > :last-child {
  margin-bottom: 0;
}

.footnotes {
  margin-top: 100px;
}

.embed-image {
  margin: auto;
  display: block;
  width: 70%;
  margin-top: 20px;
  margin-bottom: 20px;
}

iframe {
  margin: auto;
  display: block;
  margin-top: 20px;
  margin-bottom: 20px;
}

// * Post -------------------------------------------------------------------

.post-header {
  margin-bottom: 1.5em;
}

// https://www.bytedude.com/jekyll-previous-and-next-posts/#fnref:hard
.post-nav {
  padding: 20px 0px;
  margin-top: 50px;
  border-top: 3px solid rgba(227, 227, 227, 0.5);
  border-bottom: 3px solid rgba(227, 227, 227, 0.5);
  border-radius: 30pt;
}

.category-link {
  line-break: auto;
  font-size: small;
  color: gray;
  padding: 2px 8px;
  border: 1px solid rgba(227, 227, 227, 0.5);
  border-radius: 0.4em;
  margin-right: 0.5em;
  // &::before {
  //   content: "# ";
  // }
  &:hover {
    color: black;
    border-color: gray;
    text-decoration: none;
  }
}
// .post-nav .category-link {
// 	margin: auto;
// 	width: fit-content;
// 	display: block;
// 	margin-bottom: 5px;
// 	box-sizing: border-box;
// 	color: black;
// }

.post-nav-pn {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.post-nav-pn div {
  width: 50%;
}
.post-nav-pn a {
  display: block;
  flex: 1 1 0;
  margin: 10px;
  padding: 10px 20px;
  border: 1px solid lightgray;
  border-radius: 10pt;
}

.post-nav-pn a p {
  margin: 0px;
}

.post-nav-pn .post-nav-n {
  text-align: right;
}

.post-nav-label {
  color: gray;
  font-size: 12px;
}

.post-nav-pn a:hover {
  transition: $active-color 500ms cubic-bezier(0.08, 0.52, 0.52, 1);
  border-color: $active-color;
  text-decoration: none;
}

// * Lists ---------------------------------------------------------------------

.post-list > li {
  border-left: 3px solid lightgray;
  padding-left: 15px;
  margin-bottom: 3em;
}

.post-list {
  margin-top: 40px;
}

// * Table of Contents ---------------------------------------------------------

#toc-container {
  ul {
    list-style-type: none;
    margin-top: 5px;
    li {
      margin-top: 5px;
      margin-bottom: 5px;
    }
  }
}

#toc {
  margin-left: 0px;
  margin-bottom: 0px;
  padding: 0px;
  ul {
    padding-left: 1.2em;
  }
}

.toc-top-link a {
  color: gray !important;
}

// .gdocs-embed,
.html-screenplay {
  @media only screen and (max-width: $lg-breakpoint) {
    display: none;
  }
}

// * Footer --------------------------------------------------------------------

.site-footer .trigger {
  line-height: normal;
}

.footer-heading a {
  color: black;
}

// * CODEBLOCK -----------------------------------------------------------------

// [class*='language-']
code {
  border: initial;
  font-size: 1em !important;
  padding: initial;
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue",
    "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !important;
} /* TODO: Remove important, */

pre > code {
  font-size: 1.1em !important;
  color: black;
  white-space: pre-wrap;
  border: 20px;
}

pre {
  padding: 10px 15%;
  border: 0px;
  background-color: #f2f3f7;
  overflow: hidden;
}

@media only screen and (max-width: 800px) {
  pre {
    padding: 10px 3%;
  }
}
