  color: var(--theme-color) !important;
  background-color: initial;
}

pre code {
  color: black !important;
  font-family: "Courier New", Courier, monospace !important;
}

html pre code.language-screenplay-edited {
  color: var(--theme-color) !important;
}

strong code {
  text-decoration: underline;
}

/* -------------------------------- BOOTSTRAP ------------------------------- */

.page-link:hover {
  text-decoration: underline;
}

@mixin custom-scroll($width) {
  &::-webkit-scrollbar {
    width: $width;
  }
  /* Track */
  &::-webkit-scrollbar-track {
    background: #f9f9f9;
    border-left: #ececec 1px solid;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #c5c5c5;
    border-radius: 6em;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #848484;
  }
}

body {
  @include custom-scroll(6px);
}
.offcanvas-body {
  @include custom-scroll(3px);
}

body a {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

a.active {
  border-color: white;
  border-width: 1px;
  border-style: solid;
}

.page-content img {
  max-width: 100%;
}

@media only screen and (max-width: $lg-breakpoint) {
  .invis-interactable {
    text-decoration: underline !important;
  }
}

#offcanvasToggleButton {
  position: fixed !important;
  top: 5px;
  right: 5px;
  background-color: $background-color;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  z-index: 1;

  svg {
    fill: #424242;
    padding: 5px;
  }

  @media only screen and (min-width: $lg-breakpoint) {
    top: 15px;
    right: 15px;
  }

  @media only screen and (min-width: $xl-breakpoint) {
    z-index: 1046;
  }
}

#offcanvasNavbar.show + #canvasBody #nav-links {
  @media only screen and (max-width: $xl-breakpoint) {
    display: none !important;
  }
}

#canvasBody {
  transition: none;
  max-width: 70ch;
}

#offcanvasNavbar {
  transition: none;

  .page-link {
    margin-left: 0px;
    margin-right: 0px;
  }

  @media only screen and (min-width: $lg-breakpoint) {
    max-width: 38ch;
    &.show + #canvasBody {
      transform: translateX(20%);
    }
  }

  @media only screen and (min-width: $xl-breakpoint) {
    max-width: 38ch;
    &.show + #canvasBody {
      transform: translateX(30%);
    }
  }
}

#body {
  padding-bottom: 30vh;
}
